.card {
  font-size: 1.6rem;
  color: white;
  width: 35vw;
  height: 40vh;
  display: flex;
  align-items: center;
  background:

    linear-gradient(0deg, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.8)),
